(function() {
    'use strict';

    angular
        .module('pru1App')
        .controller('CiudadDetailController', CiudadDetailController);

    CiudadDetailController.$inject = ['$scope', '$rootScope', '$stateParams', 'DataUtils', 'entity', 'Ciudad', 'Pais'];

    function CiudadDetailController($scope, $rootScope, $stateParams, DataUtils, entity, Ciudad, Pais) {
        var vm = this;

        vm.ciudad = entity;
        vm.byteSize = DataUtils.byteSize;
        vm.openFile = DataUtils.openFile;

        var unsubscribe = $rootScope.$on('pru1App:ciudadUpdate', function(event, result) {
            vm.ciudad = result;
        });
        $scope.$on('$destroy', unsubscribe);
    }
})();
