(function() {
    'use strict';

    angular
        .module('pru1App')
        .controller('CiudadDialogController', CiudadDialogController);

    CiudadDialogController.$inject = ['$timeout', '$scope', '$stateParams', '$uibModalInstance', 'DataUtils', 'entity', 'Ciudad', 'Pais'];

    function CiudadDialogController ($timeout, $scope, $stateParams, $uibModalInstance, DataUtils, entity, Ciudad, Pais) {
        var vm = this;

        vm.ciudad = entity;
        vm.clear = clear;
        vm.datePickerOpenStatus = {};
        vm.openCalendar = openCalendar;
        vm.byteSize = DataUtils.byteSize;
        vm.openFile = DataUtils.openFile;
        vm.save = save;
        vm.pais = Pais.query();

        $timeout(function (){
            angular.element('.form-group:eq(1)>input').focus();
        });

        function clear () {
            $uibModalInstance.dismiss('cancel');
        }

        function save () {
            vm.isSaving = true;
            if (vm.ciudad.id !== null) {
                Ciudad.update(vm.ciudad, onSaveSuccess, onSaveError);
            } else {
                Ciudad.save(vm.ciudad, onSaveSuccess, onSaveError);
            }
        }

        function onSaveSuccess (result) {
            $scope.$emit('pru1App:ciudadUpdate', result);
            $uibModalInstance.close(result);
            vm.isSaving = false;
        }

        function onSaveError () {
            vm.isSaving = false;
        }

        vm.datePickerOpenStatus.fecha = false;

        vm.setImagen = function ($file, ciudad) {
            if ($file && $file.$error === 'pattern') {
                return;
            }
            if ($file) {
                DataUtils.toBase64($file, function(base64Data) {
                    $scope.$apply(function() {
                        ciudad.imagen = base64Data;
                        ciudad.imagenContentType = $file.type;
                    });
                });
            }
        };
        vm.datePickerOpenStatus.fechaHora = false;

        vm.setMuscia = function ($file, ciudad) {
            if ($file) {
                DataUtils.toBase64($file, function(base64Data) {
                    $scope.$apply(function() {
                        ciudad.muscia = base64Data;
                        ciudad.musciaContentType = $file.type;
                    });
                });
            }
        };

        vm.setArchivo = function ($file, ciudad) {
            if ($file) {
                DataUtils.toBase64($file, function(base64Data) {
                    $scope.$apply(function() {
                        ciudad.archivo = base64Data;
                        ciudad.archivoContentType = $file.type;
                    });
                });
            }
        };

        function openCalendar (date) {
            vm.datePickerOpenStatus[date] = true;
        }
    }
})();
