(function() {
    'use strict';
    angular
        .module('pru1App')
        .factory('Ciudad', Ciudad);

    Ciudad.$inject = ['$resource', 'DateUtils'];

    function Ciudad ($resource, DateUtils) {
        var resourceUrl =  'api/ciudads/:id';

        return $resource(resourceUrl, {}, {
            'query': { method: 'GET', isArray: true},
            'get': {
                method: 'GET',
                transformResponse: function (data) {
                    if (data) {
                        data = angular.fromJson(data);
                        data.fecha = DateUtils.convertLocalDateFromServer(data.fecha);
                        data.fechaHora = DateUtils.convertDateTimeFromServer(data.fechaHora);
                    }
                    return data;
                }
            },
            'update': {
                method: 'PUT',
                transformRequest: function (data) {
                    data.fecha = DateUtils.convertLocalDateToServer(data.fecha);
                    return angular.toJson(data);
                }
            },
            'save': {
                method: 'POST',
                transformRequest: function (data) {
                    data.fecha = DateUtils.convertLocalDateToServer(data.fecha);
                    return angular.toJson(data);
                }
            }
        });
    }
})();
